@import "https://fonts.googleapis.com/css?family=Roboto:400,700";
:root {
  --heading-height: 10em;
  --color-body: #040c0e;
  --color-aside: #132226;
  --color-li: #be9063;
  --color-active-li: #040c0e;
  --background-color-active-li: #a4978e;
  --primary-color: #525b56;
  --secondary-color: #be9063;
  --url-header: url("https://visme.co/blog/wp-content/uploads/2016/01/73.jpg")  ;
  font-size: 1em;
}

body {
  min-height: 100vh;
  min-height: 100vh;
  background-color: var(--color-body);
  color: #e7e7e7;
  vertical-align: baseline;
  border: none;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 100%;
  line-height: 1;
}

body h1 {
  color: #e7e7e7;
  width: 100vw;
  text-align: center;
}

body input[type="text"], body input[type="date"], body select {
  height: 42px;
  box-shadow: 0 0 1px 1px var(--color-aside);
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  border: none;
  border-radius: 20px;
  margin: 10px 0;
  padding: 0 10px;
}

body select {
  background-image: url("/sp-app/public/img/arrow.png");
  background-position: right 15px center;
  background-repeat: no-repeat;
  background-size: 1.5em;
}

body label {
  margin-top: 10px;
  padding-left: 15px;
}

body h2 {
  color: red;
}

body button.del-person {
  color: #fff;
  height: 20px;
  width: 20px;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: flex-start;
  font-size: 1em;
  font-weight: bold;
  display: flex;
}

body button.del-person:hover {
  background-color: #c33535;
  box-shadow: 0 0 8px 1px #dedede;
}

body main {
  z-index: 2;
  top: calc(var(--heading-height)  - 4em);
  height: calc(100vh - var(--heading-height) );
  width: calc(100vw - 15em);
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  display: flex;
  position: fixed;
  left: 15em;
}

body div.list-person {
  border-bottom: 2px solid #2980b9;
  border-radius: 0 0 10px 10px;
  margin: 0 50px 20px;
  display: flex;
  overflow: auto;
}

body a.add-person {
  text-transform: uppercase;
  color: #fff;
  background: rgba(33, 63, 86, .82);
  border-radius: 5px;
  padding: 5px 9px;
  font-weight: bold;
  text-decoration: none;
}

body a.add-person:hover {
  background: rgba(147, 201, 243, .82);
  box-shadow: 0 0 8px 1px #dedede;
}

body .styled-table {
  border-collapse: collapse;
  min-width: 400px;
  color: #444;
  margin: 25px 0;
  font-family: sans-serif;
  font-size: .9em;
  box-shadow: 0 0 20px rgba(0, 0, 0, .15);
}

body .styled-table thead tr {
  color: #fff;
  text-align: left;
  background-color: #2980b9;
}

body .styled-table th, body .styled-table td {
  width: calc(50vw - 50px);
  padding: 12px 15px;
}

body .styled-table tbody tr {
  background-color: #fff;
  border-bottom: thin solid #ddd;
}

body .styled-table tbody tr:nth-of-type(2n) {
  background-color: #f3f3f3;
}

body .styled-table tbody tr.active-row {
  color: #009879;
  font-weight: bold;
}

body .form-ajout {
  width: calc(100% - 100px);
  flex-direction: column;
  justify-content: center;
  display: flex;
}

body .valider-ajout, body .annuler-ajout {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  border: none;
  border-radius: 5px;
  margin: 10px 0 0 10px;
  padding: 10px 25px;
  font-size: 1em;
}

body .valider-ajout:hover, body .annuler-ajout:hover {
  background-color: #2a772a;
  box-shadow: 0 0 8px 1px #dedede;
}

body .annuler-ajout {
  background-color: var(--secondary-color);
  text-decoration: none;
}

body .annuler-ajout:hover {
  background-color: #72552a;
}

body div.action {
  justify-content: flex-end;
  display: flex;
}

body div.event-name {
  width: 100%;
  justify-content: center;
  margin: 20px;
  display: flex;
}

body img.logo-accueil {
  max-width: 50vw;
  border: 1px solid #1c1b20;
  border-radius: 25px;
  box-shadow: 1px 1px 12px 1px #fefefe;
}

header {
  z-index: 2;
  width: 100%;
  height: var(--heading-height);
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: none;
  margin: 0;
  padding: 0;
  position: fixed;
}

header > div > a {
  align-items: center;
  margin: .8em;
  display: flex;
}

header:before {
  content: "";
  width: 100%;
  height: calc(var(--heading-height)  + 10em);
  z-index: -1;
  background: linear-gradient(rgba(0, 0, 0, .6), rgba(0, 0, 0, .6)), var(--url-header) no-repeat center, linear-gradient(#4e4376, #2b5876);
  background-size: cover;
  border-bottom: .2em solid #fff;
  display: block;
  position: absolute;
  bottom: 6em;
  left: 0;
  transform: skewY(-3.5deg);
}

header a {
  color: #f1f0f0;
  font-size: xx-large;
  text-decoration: none;
}

header img.icon {
  width: 32px;
  filter: invert();
  margin-right: 20px;
}

aside div.menu {
  width: 15em;
  background-color: var(--color-aside);
  padding-top: calc(var(--heading-height)  + 2em);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: .5px 0 3px 2px #fefefe;
}

aside div.menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

aside div.menu ul ul li {
  cursor: pointer;
  padding-left: 30px;
}

aside div.menu ul li {
  color: var(--color-li);
  padding: 0 10px;
}

aside div.menu ul li:hover {
  background-color: var(--background-color-active-li);
}

aside div.menu ul li:hover a {
  color: var(--color-active-li);
}

aside div.menu ul li i.fas:before, aside div.menu ul li svg {
  margin-right: 5px;
}

aside div.menu ul li a {
  color: var(--color-li);
  min-height: 45px;
  flex: 1;
  align-items: center;
  text-decoration: none;
  display: flex;
}

aside div.menu ul li.active {
  background-color: var(--background-color-active-li);
  color: var(--color-active-li);
  min-height: 45px;
  align-items: center;
  display: flex;
}

aside div.menu ul li.active a {
  color: var(--color-active-li);
}

footer {
  height: 70px;
  color: #e7e7e7;
  text-transform: uppercase;
  border-top: 5px solid #fff;
  border-radius: 15%;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  font-size: 25px;
  font-weight: bold;
  display: flex;
  position: fixed;
  bottom: 0;
  left: calc(10em + 30px);
  right: 30px;
}

footer img {
  height: 50px;
  filter: invert(.1);
  margin-top: 10px;
}

